
import { defineComponent } from 'vue'
import { COM21700111_REQ_BODY, COM21700111_RES, MessageInformation } from '@/shared/type/COM21700111.type';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import { PAGE_SIZE } from "@/shared/constants/common.const";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';

export default defineComponent({
    name: "com216004sc00",
    components: {
        Pagination,
        SelectOption,
        BreadCrumb
    },
    data() {
        const headers = [
            { columnName: "No" },
            { columnName: "View" },
            { columnName: "Message ID" },
            { columnName: "Message Type Code" },
            { columnName: "Push Category" },
            { columnName: "SMS Event Type Code" },
            { columnName: "SMS Event Detail Code" },
            { columnName: "Status Code" },
            { columnName: "Reference Service ID" },
            { columnName: "Deep Link Screen ID" },
            { columnName: "Event Description" }
        ];

        return {
            headers,
            messageID: '',
            statusCode: '',
            messageTypeCode: '',
            messageList: [] as MessageInformation[],
            pageSize: PAGE_SIZE,
            pageNumber: 1,
            startingNo: 1,
            resetPage: false,
            totalCountMessage: 0
        }
    },

    mounted() {
        this.handleGetMessage();
    },

    computed: {
        messageNo() {
            return (index: number, startingNo: number) => index + startingNo;
        },

        messageStatusCode(): { key: string, value: string }[] {
            return this.$store.state.CommonCodeModule.commonCode.StatusCode;
        }
    },

    methods: {
        async handleGetMessage(mode?: string) {
            const reqBody: COM21700111_REQ_BODY = {
                messageID: this.messageID,
                statusCode: this.statusCode,
                messageTypeCode: this.messageTypeCode,
                pageSize: PAGE_SIZE,
                pageNumber: mode == 'search' ? 0 : this.pageNumber,
            }

            if (mode === 'search') {
                this.pageNumber = 1
                this.resetPage = true;
            }

            const responseData = (await this.$request.getRequest("COM21700111", reqBody, false)) as COM21700111_RES;

            this.messageList = responseData.body.list;
            this.totalCountMessage = responseData.body.totalCount;
        },

        handleRegister() {
            this.$router.push({
                path: '/COM216004SC01',
                query: {
                    type: "register"
                }
            });
        },

        handleRowClick(rowData: any) {
            this.$router.push({
                path: 'COM216004SC01',
                query: {
                    type: "edit",
                    messageID: rowData.messageID,
                }
            });
        },

        handlePagination(pageNumber: number) {
            this.pageNumber = pageNumber;
            this.resetPage = false;
            this.handleGetMessage();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingNo = startingIndex;
        },

        resetForm() {
            this.resetPage = true;
            this.pageNumber = 1;
            this.statusCode = '';
            this.messageID = '';
            this.messageTypeCode = '';

            this.handleGetMessage()
        }
    }


})
